.copyright {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    padding-top: 22px;
    padding-bottom: 22px;

    @media (min-width: $tablet-width) {
       padding-top: 20px;
       width: 190px;
       
    }
}

.copyright__link {
    order: -1;
    font-size: 16px;
    line-height: 18px;
    cursor: pointer;
   
    @media (min-width: $tablet-width) {
        margin-left: 25px;
     }
}